*,
*::after,
*::before {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  user-select: none;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  color: var(--text);
}

::placeholder {
  opacity: .6;
  transition: opacity .2s;
}

:focus::placeholder {
  opacity: 0;
}

input,
textarea {
  background: transparent;
}

button.ui-button {
  padding: 3px 14px;
  color: var(--button-color);
  background: var(--button);
  border: 1px solid var(--button-border);
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
  background-clip: border-box;
  font-size: 13px;
  border-radius: 5px;
}

button.ui-button.small {
  font-size: 11px;
  border-radius: 3px;
  padding: 2px 12px;
}

button.ui-button:focus {
  color: #fff;
  background: var(--accent);
  background-image: linear-gradient(var(--accent-light), var(--accent));
  border: 1px solid var(--accent);
  outline: none;
}

button.ui-button:active {
  color: #fff;
  background: var(--accent-dark);
  background-image: linear-gradient(var(--accent), var(--accent-dark));
  border: 1px solid var(--accent-dark);
}

button.ui-button:disabled {
  opacity: .5;
}

[data-theme^="dark"] ::placeholder {
  color: #fff;
}

body.resize {
  background-image: var(--resize);
  background-repeat: no-repeat;
  background-position: right bottom;
}

header {
  padding: 10px 40px;
  background-color: var(--modal-header);
  color: var(--text-color);
  font-size: 13px;
  line-height: 18px;
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.scroll {
  position: fixed;
  top: 38px;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: scroll;
}

form {
  padding: 20px 40px 0;
}

form ul {
  list-style-type: none;
}

form ul li {
  padding-bottom: 10px;
}

form ul li label {
  color: var(--text);
  display: block;
  float: left;
  width: 80px;
  text-align: right;
  padding-right: 10px;
  font-size: 13px;
  line-height: 28px;
}

form ul li label::after {
  content: ':';
}

form ul li input,
form ul li textarea {
  display: block;
  padding-left: 10px;
  padding-right: 28px;
  width: calc(100% - 80px);
  color: var(--input-color);
  background-color: var(--input-background);
  border: 1px solid var(--input-border);
  background-position: right 6px top 6px;
  background-repeat: no-repeat;
  transition: background-color .15s;
}

form ul li input[required]:invalid:not(:placeholder-shown),
form ul li textarea[required]:invalid:not(:placeholder-shown) {
  background-image: var(--error);
}

form ul li input[required]:valid,
form ul li textarea[required]:valid {
  background-image: var(--check);
}

form ul li input {
  height: 28px;
}

form ul li textarea {
  height: 44px;
  resize: none;
}

form ul li .select2-container--custom .select2-selection {
  box-shadow: none;
  color: var(--input-color);
  background-color: var(--input-background);
  border: 1px solid var(--input-border);
}

footer {
  text-align: right;
  padding: 15px 0 0;
}

footer button {
  padding: 4px 8px;
  border: 1px solid #C5C3C5;
  border-bottom-color: #9F9E9F;
  background: #fff;
  border-radius: 4px;
}

footer button:active {
  color: #fff;
  border-color: #237DFE;
  border-bottom-color: #0240DD;
  background-image: linear-gradient(-180deg, #4A97FD 0%, #0968E5 100%);
}

[data-theme="dark"] form ul li input,
[data-theme="dark"] form ul li textarea,
[data-theme="dark"] form ul li .select2-container--custom .select2-selection {
  border-top: 0;
  border-left: 0;
  border-right: 0;
  box-shadow: inset 0 0 1px rgba(255, 255, 255, 0.5), inset 0 6px 4px -4px rgba(0, 0, 0, 0.2);
}

[data-theme="dark"] form ul li .select2-container--custom .select2-selection--multiple .select2-search--inline .select2-search__field {
  box-shadow: none;
}

.select2-container--custom {
  display: block;
}

.select2-container--custom .select2-selection {
  font-size: 13px;
  outline: 0;
}

.select2-container--custom .select2-selection.form-control {
  border-radius: 4px;
}

.select2-container--custom .select2-search__field {
  outline: 0;
}

.select2-container--custom .select2-results__option {
  padding: 7px 12px;
  color: var(--text);
}

.select2-container--custom .select2-results__option[aria-selected=true] {
  background-color: var(--select-sort);
  color: var(--text);
}

.select2-container--custom .select2-results__option--highlighted[aria-selected] {
  background-color: var(--accent);
  color: #fff;
}

.select2-container--custom.select2-container--focus .select2-selection, .select2-container--custom.select2-container--open .select2-selection {
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 6px var(--accent);
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  border-color: var(--accent);
}

.select2-container--custom .select2-dropdown {
  box-shadow: 0 0px 15px 6px rgba(0, 0, 0, 0.175);
  border-color: var(--input-border);
  overflow-x: hidden;
}

.select2-container--custom .select2-dropdown--above {
  box-shadow: 0 0px 15px 6px rgba(0, 0, 0, 0.2);
}

.select2-container--custom .select2-dropdown,
.select2-container--custom .select2-dropdown--above {
  position: fixed !important;
  top: auto !important;
  left: 40px;
  right: 0;
  padding-top: 4px;
}

.select2-container--custom .select2-results > .select2-results__options {
  max-height: 134px;
  overflow-y: auto;
}

.select2-container--custom .select2-selection--multiple .select2-selection__rendered {
  box-sizing: border-box;
  display: block;
  line-height: 1.42857143;
  list-style: none;
  margin: 0;
  overflow: hidden;
  padding: 0;
  width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.select2-container--custom .select2-selection--multiple .select2-selection__choice {
  color: #fff;
  background: var(--accent-light);
  border: 1px solid var(--accent-dark);
  border-radius: 3px;
  cursor: default;
  float: left;
  margin: 5px 0 0 6px;
  padding: 0 6px;
}

.select2-container--custom .select2-selection--multiple .select2-search--inline .select2-search__field {
  background: transparent;
  padding: 0 12px;
  height: 32px;
  line-height: 1.42857143;
  margin-top: 0;
  min-width: 5em;
}

.select2-container--custom .select2-selection--multiple .select2-selection__choice__remove {
  color: #fff;
  display: inline-block;
  font-weight: bold;
  margin-right: 3px;
}

.select2-container--custom .select2-selection--multiple .select2-selection__choice__remove:hover {
  color: #fff;
  opacity: .6;
}

.select2-selection--multiple {
  margin-left: 80px;
  width: 320px;
}

.select2-dropdown {
  margin-left: 80px;
  font-size: 13px;
  border: 1px solid var(--table-border);
  border-radius: 4px;
  background-color: var(--menu);
}
